.footer {
  background-color: #2c3e50;
  color: #ecf0f1;
  padding: 30px 0;
  font-family: 'Arial', sans-serif;
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.1);
}

.footer-container {
  display: flex;
  justify-content: space-around;
  max-width: 85%;
  margin: 0 auto;
  background-color: #34495e;  /* Slightly darker background for the container */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.footer-container:hover {
  transform: translateY(-10px); /* Moves the tile up */
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2); 
  height: auto;
  width: 96%;
}

.footer-column {
  flex: 1;
  padding: 0 20px;
}

.footer-title {
  font-size: 1.5em;
  margin-bottom: 20px;
}

.footer-links {
  list-style: none;
  padding: 0;
}

.footer-links li {
  margin-bottom: 10px;
}

.footer-links a {
  color: #bdc3c7;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-links a:hover {
  color: #ecf0f1;
}

.social-icons {
  display: flex;
  margin-top: 10px;
}

.social-icon {
  color: #bdc3c7;
  font-size: 20px;
  margin-right: 15px;
  transition: color 0.3s;
}

.social-icon:hover {
  color: #ecf0f1;
}

.footer-bottom {
  text-align: center;
  margin-top: 20px;
}

.footer-bottom p {
  font-size: 0.8em;
  color: #bdc3c7;
}